.outerquestionsdiv{
    /* border: 2px solid red; */
    height: fit-content;
    padding: 2em;
}
.buttons{
    /* border: 2px solid red; */
    display: flex;
    /* width: 33%; */
    margin: 0 auto;
    padding: 0.3em 1em;   
    justify-content: center;
}
button{
    margin: 1em;
}
#removebtn{
    /* border: 2px solid red; */
    width: 33%;
    margin: 1em 0 3em 0 ;

}
#formdiv{
    /* border: 2px solid green; */
    text-align: center;
}
.quizid{
    background-color: #ffffff;
    border: 1px solid black;
}
#qui{
    margin-bottom: 50px;
}
.field{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.sub{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
#add{
    margin: auto;
    background-color: white;
    width: 150px;
    display: flex;
    justify-content: center;
    height: 50px;
    align-items: center;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
}