body{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
html{
    /* border: 10px solid black; */
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    /* height: 100%; */
}

.container{
    padding: 0em 1.5em;
    margin: 0px 0px;
    /* border: 2px solid red; */
}
a{
    /* color: aqua; */
    text-decoration: none;
}
.hero{
    /* border: 2px solid red; */
    display: flex;
    position:sticky;
    background-color:  rgba(255, 255, 255, 0.96);
    /* width: 100%; */
    /* left: 0px; */
    top: 0px;
    z-index: 100;
    box-shadow: 0 0 5px #878181;
    align-items: center;
    padding: 0.5em 1em;
    justify-content: space-between;
}
.logo{
    /* border: 2px solid red; */
    max-height: 100%;
    max-width: 100%;
    padding: 0em;
}
.logo1{
    /* max-width: 65%;
    min-height: 100%; */
    width: 200px;
}
.login{
    color: #545e6f;
    /* text-decoration:solid dotted; */
    font-weight: 500;   
}
.inmobileview{
    /* border: 2px solid red; */
    background-color: rgba(255, 255, 255, 0.96);
    position: absolute;
    top: 65px;
    left: 0px;
    z-index: 100;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 1em 0.5em;
    text-align: center;
    /* box-shadow: 0 0 5px #878181; */
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
@media  (min-width: 1000px) {
    .itemlist{
        display: inline-block;
        width: 100%;
    }
    .List{
       /* border: 2px solid green; */
       width: 60%;
       display: flex;
       flex-direction: row;
       list-style: none;
       justify-content: space-around;
       margin: 0.5em 0;
    }
    
    .listItem{
        /* border: 2px solid red; */
        font-size: 1rem;
        align-items: center;
        text-decoration: none;
        padding: 0.5em 0;
    }

    /* .listItem{ */
        /* border: 2px solid red; */
    /* } */
    .listItem:after {    
        background: none repeat scroll 0 0 transparent;
        bottom: 0;
        content: "";
        display: block;
        height: 3px;
        left: 50%;
        position: relative;
        background: rgb(187, 132, 232);
        transition: width 0.3s ease 0s, left 0.3s ease 0s;
        width: 0;
        top: 5px;
      }
    .listItem:hover:after { 
        width: 100%; 
        left: 0; 
      }
    .hamburger-menu{
        display: none;
    }
    .inmobileview{
        display: none;
    }
    .logo{
        /* border: 2px solid red; */
        height: 100%;
        width: 100%;
        padding: 0 2em;
    }
    .logo1{
        /* max-width: 35%; */
        width: 250px;
    }
  
    .navemail{
        /* border: 2px solid bisque; */
        padding: 0em 1em;
        display: flex;
        align-items: center;
    }
    .navcall{
        /* border: 2px solid red; */
        padding: 0em 1em;
        display: flex;
        align-items: center;
    }
  }