.whatsapp{
    /* border: 2px solid red; */
    display: inline-block;
    position: fixed;
    bottom: 20px;
    right: 20px;  
    height: 50px;
    width: 50px;
}
.whatsapp:hover{
    cursor: pointer;
}
.sticker{
    /* border: 2px solid red; */
