*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
	/* background: linear-gradient(-45deg, #bb84e8, #8a52b8, #bc9ad7, #8a52b8);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	height: 100vh; */
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.formouter{
    /* border: 2px solid red; */
    padding: 4em 2em;
    width: 100%;
    /* background-color: rgb(187, 132, 232); */
    height: 100%;
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    /* background: linear-gradient(-45deg, #bb84e8, #8a52b8, #bc9ad7, #8a52b8); */
    
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
}
form {
  margin: 0 auto;
  width: 35%;
  /* border: 2px solid red; */
  /* background-color: rgba(95, 22, 154, 0.351); */
  background-color: rgba(190, 141, 230, 0.493);
  border-radius: 10px;
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  padding: 50px 35px;
  font-weight: 700;
  /* border: 2px solid red; */

}

form * {
  font-family: "Poppins", sans-serif;
  color: #000000;
  letter-spacing: 0.5px;
  outline: none;
  border: none;
}

@media (prefers-color-scheme: dark) {
  form * {
    color: #000000;
  }
}

form h4 {
  font-size: 25px;
  font-weight: 700;
  line-height: 42px;
  text-align: center;
}

label {
  display: block;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 500;
}
input {
  display: block;
  height: 50px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.07);
  border-radius: 3px;
  padding: 0 10px;
  margin-top: 8px;
  font-size: 14px;
  font-weight: 400;
}

::placeholder {
  color: #1b1b1b;
}

@media (prefers-color-scheme: dark) {
  ::placeholder {
    color: rgba(0, 0, 0, 0.567);
  }
}

button {
  margin: 2em 0 0 0;
  width: 150px;
  height: 55px;
  align-items: center;
  background-color: #ffffff;
  color: #080710;
  padding: 15px 0;
  font-size: 17px;
  font-weight: 600;
  border-radius: 5px;
  cursor: pointer;
}
.buttonsdiv{
  /* border: 2px solid red; */
  align-items: center;
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 0 auto;
}
.social {
  margin-top: 30px;
  display: flex;
}
.social div {
  /* background: red; */
  width: 150px;
  border-radius: 3px;
  padding: 5px 10px 10px 5px;
  background-color: rgba(255, 255, 255, 0.27);
  color: #150f03;
  text-align: center;
}

#viewquiz{
  backdrop-filter: blur(30px);
    -webkit-backdrop-filter: blur(30px);
    background-color: rgba(190,141,230,.493);
    border: 2px solid hsla(0,0%,100%,.1);
    border-radius: 10px;
    box-shadow: 0 0.0625em 0.0625em rgba(0,0,0,.25), 0 0.125em 0.5em rgba(0,0,0,.25), inset 0 0 0 1px hsla(0,0%,100%,.1);
    font-weight: 700;
    margin: 0 auto;
    padding: 30px 35px;
    width: 35%;
    display: flex;
    margin: 0 auto;
    margin-bottom: 1.5em;
}
